

html, body {
    margin: 0;
    height: 100%;
}

body {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#left-side {
    width: 35%;
    height: 100%;
    background-color: rebeccapurple;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 25%;
}

#title-bar {
    background-color: black;
    height: 15%;
    width: 100%;
    opacity: 50%;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#title-img {
    opacity: 100%;
    height: 140px;
    width: 105px;
}

#title-text {
    opacity: 100%;
    color: white;
}

#right-side {
    width: 65%;
    height: 100%;
    background-color: turquoise;
}